import Styled from "./style"
import { Link } from "react-router-dom"
import { bubble as Menu } from 'react-burger-menu'
import { useState } from "react"

export default function Headerphone() {
    const [isOpen, setOpen] = useState(false)

    const handleIsOpen = () => {
      setOpen(!isOpen)
    }
  
    const closeSideBar = () => {
      setOpen(false)
    }
    return (
        <Styled>
            <nav>
                <ul>
                    <Menu 
                    right 
                    width={ '100%' }    
                    isOpen={isOpen}
                    onOpen={handleIsOpen}
                    onClose={handleIsOpen}
                    >
                    <li onClick={closeSideBar}><Link to="/portfolio/" className="firstlink">Accueil</Link></li>
                    <li onClick={closeSideBar}><Link to="/portfolio/about" className="secondlink">A propos</Link></li>
                    <li onClick={closeSideBar}><Link to="/portfolio/projects" className="thirdlink">Projets</Link></li>
                    <li onClick={closeSideBar}><Link to="/portfolio/contact" className="fourthlink" >Contact</Link></li>
                    </Menu>
                </ul>
            </nav>
        </Styled>)
}