import React from 'react';
import 'reset-css';
import Header from './components/Headerdesk/Headerdesk.jsx';
import Styled from './style';
import Footer from './components/Footer/Footer.jsx';
import { useMediaQuery } from 'react-responsive'

import AnimatedRoutes from './pages/AnimatedRoutes.jsx';
import Headerdesk from './components/Headerdesk/Headerdesk.jsx';
import Headerphone from './components/Headerphone/Headerphone.jsx';

function App() {
  const isDesktopOrLaptop = useMediaQuery({
    query: '(min-width: 1024px)'
  })
  const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1024px)' })
  return (
    <Styled>
    {isDesktopOrLaptop && <Headerdesk/>}
    {isTabletOrMobile && <Headerphone/>}
      <AnimatedRoutes />
    </Styled>
  )
}

export default App;
